.nav-tabs {
    height: 64px;
    white-space: normal;
    padding-top: 10px !important;
}

.nav-tabs-active {
    background: -webkit-gradient(
        linear,
        left top, left bottom,
        from(transparent),
        color-stop(transparent),
        to(hsla(38, 15%, 56%, 0.4))
    );
    background: -webkit-linear-gradient(
        transparent,
        transparent,
        hsla(38, 15%, 56%, 0.4)
    );
    background: -o-linear-gradient(
        transparent,
        transparent,
        hsla(38, 15%, 56%, 0.4)
    );
    background: linear-gradient(
        transparent,
        transparent,
        hsla(38, 15%, 56%, 0.4)
    );
}

.logo {
    height: 70%;
    width: auto;
    margin-top: 8px;
    margin-left: 10px;
}
.navigation-bar-left {
    display: -ms-flexbox;
    display: flex;
}
.navigation-bar-right {
    display: -ms-flexbox;
    display: flex;
}

.navigation-item {
    color: #f1e6d0;
    line-height: 60px;
    text-align: center;
    text-decoration: none !important;
    padding-left: 7px;
    padding-right: 7px;
    border-bottom: 2px transparent solid;
    position: relative;
    display: block;
    font-size: 13px;
}
.navigation-item:after {
    content: '';
    position: absolute;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    opacity: 0;
    -webkit-transition: all 0.25s linear;
    -o-transition: all 0.25s linear;
    transition: all 0.25s linear;
    background: -webkit-gradient(
        linear,
        left top, left bottom,
        from(transparent),
        color-stop(transparent),
        to(hsla(0, 0%, 90%, 0.6))
    );
    background: -webkit-linear-gradient(
        transparent,
        transparent,
        hsla(0, 0%, 90%, 0.6)
    );
    background: -o-linear-gradient(
        transparent,
        transparent,
        hsla(0, 0%, 90%, 0.6)
    );
    background: linear-gradient(
        transparent,
        transparent,
        hsla(0, 0%, 90%, 0.6)
    );
}
.navigation-item:hover:after {
    color: white;
    opacity: 1;
}

.navigation-item-active {
    color: white;
    background: -webkit-gradient(
        linear,
        left top, left bottom,
        from(transparent),
        color-stop(transparent),
        to(hsla(0, 0%, 80%, 0.4))
    );
    background: -webkit-linear-gradient(
        transparent,
        transparent,
        hsla(0, 0%, 80%, 0.4)
    );
    background: -o-linear-gradient(
        transparent,
        transparent,
        hsla(0, 0%, 80%, 0.4)
    );
    background: linear-gradient(
        transparent,
        transparent,
        hsla(0, 0%, 80%, 0.4)
    );
}

.navigation-item-active:after {
    background-image: url(../res/images/pointer.png);
    background-position: top;
    background-repeat: no-repeat;
    opacity: 1;
    -webkit-transition-duration: 0s;
         -o-transition-duration: 0s;
            transition-duration: 0s;
}

.navigation-notification {
    margin: 15px;
    cursor: pointer;
    height: 30px;
    width: 30px;
    overflow: hidden;
    background: url(../res/images/notificationbuttons.png) 0 -80px;
}

.navigation-notification.active {
    background: url(../res/images/notificationbuttons.png) 0 0px !important;
}

.navigation-notification:hover {
    background: url(../res/images/notificationbuttons.png) 0 -40px;
}



